import React, {useEffect, useState} from 'react';
import {useSelector} from "react-redux";
import {Box} from "@mui/material";
import {useNavigate} from "react-router-dom";

const UserMyPage = () => {
    const navigate = useNavigate();
    const {localhost} = useSelector((store) => store);
    const [phoneChanged, setPhoneChanged] = useState(false);
    const [user, setUser] = useState({
        id: "",
        username: "",
        name: "",
        phone: ""
    });

    useEffect(() => {
        //  then  promise (ticket)
        //  then   
        fetch(localhost + "/user/my-info").then(res => res.json()).then(res => {
            setUser(res);
        }); //  
    }, [phoneChanged]);

    const changePassword = (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);

        let details = {
            id: user.id,
            prePassword: data.get('prePassword'),
            newPassword: data.get('newPassword'),
        };

        console.log(details);

        fetch(localhost + "/api/user/" + details.id, {
            method: "PUT",
            headers: {
                "Content-Type": "application/json; charset=utf-8"
            },
            body: JSON.stringify(details)
        })
            .then(res => {
                console.log(1, res)
                if (res.status === 200) {
                    fetch(localhost + "/logout", {
                        method: "GET",
                    })
                        .then(res => {
                            console.log(1, res)
                            const form = res.url.substring(res.url.lastIndexOf(":"));
                            const url = form.slice(form.indexOf("/"));
                            if (res.status === 200) {
                                navigate(url);
                            }
                        })
                    alert("");
                } else {
                    alert("");
                }
            })
    }

    const changePhone = (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);

        let details = {
            id: user.id,
            phone: data.get('phone')
        };

        console.log(details);

        fetch(localhost + "/api/user/" + details.id, {
            method: "PUT",
            headers: {
                "Content-Type": "application/json; charset=utf-8"
            },
            body: JSON.stringify(details)
        })
            .then(res => {
                console.log(1, res)
                if (res.status === 200) {
                    alert("");
                    setPhoneChanged(!phoneChanged);
                } else {
                    alert("");
                }
            })
    }

    return (
        <div>
            <h1></h1>
            <table>
                <tbody>
                <tr>
                    <td></td>
                    <td>{user.name}</td>
                </tr>
                <tr>
                    <td></td>
                    <td>{user.phone}</td>
                </tr>
                </tbody>
            </table>
            <Box component="form" onSubmit={changePassword}>
                <div> </div>
                <div><input type="password" name="prePassword"/></div>
                <div> </div>
                <div><input type="password" name="newPassword"/></div>
                <button type="submit"></button>
            </Box>
            <Box component="form" onSubmit={changePhone}>
                <div> </div>
                <div><input type="text" name="phone"/></div>
                <button type="submit"></button>
            </Box>
        </div>
    );
};

export default UserMyPage;
